import React, { useEffect } from "react";
import {
  Container,
  Grid,
  makeStyles,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import arrowUp from "../../../assets/images/state/upArrow.webp";
import arrowDown from "../../../assets/images/state/downArrow.webp";
import { Link } from "react-router-dom";
import { get小urrenciesList } from "../../../redux/reducers/小urrencies";
import { useDispatch, useSelector } from "react-redux";
import { getInternalQuantity } from "../../../redux/reducers/internal";

const MoneyTransaction = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const theme = useTheme();
  const mobile = useMediaQuery(theme.breakpoints.down(576));

  useEffect(() => {
    dispatch(get小urrenciesList());
    dispatch(getInternalQuantity());
    // eslint-disable-next-line 
  }, []);

  const { internalQuantity } = useSelector(
    ({ internalReducer }) => internalReducer
  );

  return (
    <section style={{ color: "#191D23" }}>
      <Container
        maxWidth="lg"
        style={{
          display: "flex",
          justifyContent: "center",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Typography variant="h3" style={{ marginBottom: 24 }}>
          {t("moneyTransaction")}
        </Typography>
        <Grid
          container
          justifyContent="center"
          spacing={2}
          style={!mobile ? { width: "69%" } : null}
        >
          <Grid item xs={12} md={6}>
            <Link to="/dashboard/transaction/send" className={classes.grid}>
              <img src={arrowUp} alt="" />
              <Typography className={classes.title}>
                {" "}
                {t("sendFunds")}
              </Typography>
              <Typography variant="h5" className={classes.subtitle}>
                {t("sendFundsDescr")}
              </Typography>
            </Link>
          </Grid>
          <Grid item xs={12} md={6}>
            <Link to="/dashboard/transaction/history" className={classes.grid}>
              {internalQuantity.quantity > 0 && (
                <div className={classes.badge}>
                  {internalQuantity?.quantity}
                </div>
              )}
              <img src={arrowDown} alt="" />
              <Typography className={classes.title}>{t("getFunds")}</Typography>
              <Typography className={classes.subtitle} variant="h5">
                {t("getFundsDescr")}
              </Typography>
            </Link>
          </Grid>
        </Grid>
      </Container>
    </section>
  );
};

export default MoneyTransaction;

const useStyles = makeStyles((theme) => ({
  title: {
    fontSize: 33,
    fontWeight: "400",
    maxWidth: 164,
    textAlign: "center",
    color: "#FFF",
    marginBottom: 16,

    [theme.breakpoints.down(768)]: {
      fontSize: 28,
      lineHeight: "34px",
      letterSpacing: "-0.02em",
    },
  },

  subtitle: {
    color: "#B8C0CC",
    textAlign: "center",
    maxWidth: 289,

    [theme.breakpoints.down(768)]: {
      fontSize: "16px",
      lineHeight: "19px",
      maxWidth: "unset",
    },
  },

  grid: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    backgroundColor: "#121212",
    borderRadius: 16,
    padding: "40px 50px",
    border: "2px solid transparent",
    transition: "all 0.3s linear",
    position: "relative",

    [theme.breakpoints.down(768)]: {
      padding: "32px 24px",
    },

    "&:hover": {
      "& img": {
        transform: "scale(1.09)",
      },
    },

    "& img": {
      height: 152,
      transition: "all 0.2s linear",
      width: 152,

      [theme.breakpoints.down(768)]: {
        height: 100,
        width: 100,
      },
    },
  },

  badge: {
    backgroundColor: "#EF4444",
    position: "absolute",
    top: -30,
    right: -30,
    borderRadius: "100%",
    fontSize: 33,
    fontWeight: "600",
    minHeight: 56,
    minWidth: 56,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    padding: "8px 19px",

    [theme.breakpoints.down(768)]: {
      minHeight: 40,
      minWidth: 40,
      fontSize: 23,
      padding: "6px 13px",
      top: -20,
      right: -20,
    },
  },
}));
